<template>
<div id="rentCar">
   <h2>商家如何更换中控？</h2>
  <p>在工作台，车辆管理内点击更换中控。</p>
  <p>若用户取车后中控损坏或其他原因导致需要更换中控（车辆），可在此处进行更换。</p>
  <p>首先录入需要旧中控SN，再选择录入新中控SN即完成更改。</p>
  <p>更改后不影响用户继续使用，订单内SN信息同步更改。</p>
  <div class="imgDiv">
    <img src="../../assets/15.png" alt="">
  </div>
</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">
#rentCar{
 padding: 0 40px;
  box-sizing: border-box;
  h2{
    font-size:28px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 100px;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  p{
    font-size:28px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:40px;
    margin-top: 38px;
    text-align: left;
  }
  .imgDiv{
    width:400px;
    margin: 40px auto;
    margin-top: 70px;
    img{
      height: 100%;
      width: 100%;
    }
  }
}
</style>



